<template>

<div class="page-index">

    <div class="top-bgimg">
        <div class="index-header clearfix">
            <a @click="linkHome">
                <dl class="logobox">
                    <dt><img src="../../static/images/logo.png" alt="logo"></dt>
                    <dd>
                        <h3></h3>
                        <h4>www.jiayiworld.com</h4>
                    </dd>
                </dl>
            </a>
            <ul class="nav">
                <li><a @click="linkHome"></a></li>
                <li><a href="#"></a></li>
                <li><a href="#"></a></li>
                <li><a href="#"></a></li>
                <li><a href="#"></a></li>
            </ul>
            <ul class="btns">
                <li><a @click="linkLogin"></a></li>
            </ul> 
        </div>
        <div class="banner">
            <div class="bg1"><img src="../../static/images/bg2.png"></div>
            <div class="loginbox">
                <ul>
                    <li><input class="input" type="text" placeholder="" autocomplete="off" v-model="phoneNumber"></li>
                    <li class="yzmbox"><input class="input" type="password" placeholder="" autocomplete="off" v-model="yzmCode"><a class="btn" @click="getYZM"></a></li>
                    <li><input class="input" type="password" placeholder="" autocomplete="off" v-model="newPW"></li>
                    <li><input class="input" type="password" placeholder="" autocomplete="off" v-model="resetPW"></li>
                    <li><a class="btn" @click="findPassword"></a></li>
                </ul>
            </div>
            <div class="bg3"><img src="../../static/images/bg3.png"></div>
        </div>
    </div>

    <div class="footer">
        <div class="row1">010-57284115       business@jiayiworld.com    SOHO171008</div>
        <div class="row1">Copyright  2018      ICP18044271-1</div>
    </div>

</div>

</template>

<script>
import {util} from '../assets/js/util'


export default {
    name: 'forgetpw',
    data () {
        return {
          isShowErrorMessage: false,
          errorMessage: '',
          phoneNumber: '',
          yzmCode: '',
          newPW: '',
          resetPW: '',
        }
    },
    methods: {
        getYZM(){     
            if(util.trim(this.phoneNumber).length<1){
                this.$message({
                  message: '',
                  type: 'error'
                });
                return false;
            }
            let params = {
                url: 'user/getAdminVerCode',
                data: {
                    phone: this.phoneNumber,
                },
            }
            util.$http(params).then(response=>{
                console.log(response);
                if(response.data.code=='0000'){  
                  this.$message({
                    message: '',
                    type: 'success'
                  });
                }else{
                  this.$message({
                    message: '',
                    type: 'error'
                  });
                }
            }); 
        },
        findPassword(){
            this.isShowErrorMessage = false;

            if(util.trim(this.phoneNumber).length<1){
                this.$message({
                  message: '',
                  type: 'error'
                });
                return false;
            }
            if(util.trim(this.yzmCode).length<1){
                this.$message({
                  message: '',
                  type: 'error'
                });
                return false;
            }
            if(util.trim(this.newPW).length<1){
                this.$message({
                  message: '',
                  type: 'error'
                });
                return false;
            }
            if(util.trim(this.resetPW).length<1){
                this.$message({
                  message: '',
                  type: 'error'
                });
                return false;
            }
            if(this.newPW!=this.resetPW){
                this.$message({
                  message: '',
                  type: 'error'
                });
                return false;
            }

            let params = {
                url: 'user/findPassword',
                data: {
                    phone: this.phoneNumber,
                    password: this.newPW,
                    newPassWord: this.resetPW,
                    verCode: this.yzmCode
                },
            }
            
            util.$http(params).then(response=>{
                console.log(response);
                if(response.data.code=='0000'){
                    this.$router.push({name: 'login'});
                }else{
                  this.$message({
                    message: '',
                    type: 'error'
                  });
                }
            }).then(error=>{
              this.$message({
                message: '',
                type: 'error'
              });
            });         
        }, 
        linkLogin(){
            this.$router.push({name: 'login'});
        },
        linkHome(){
            this.$router.push({name: 'home'});
        },
        linkForgetpw(){
            this.$router.push({name: 'home'});
        }
    }
}
</script>

<style lang="less" scoped>
@import '../assets/less/login.less';
</style>



