<template>
  <div class="productionList">

    <div class="cont1">
        <h3 class="row1">--{{isUpdateDynamicList}}</h3>
    </div>

    <div class="cont1 clearfix" style="margin-top:40px;">
        <div class="left">
            <table>
                <tr>
                    <td></td>
                    <td>
                        <select class="input" v-model="selectRole">
                            <option value="-1"></option>
                            <option v-for="role in allRole" :value="role.roleId">{{role.userName}}</option>
                        </select>
                    </td>
                    <td style="padding-left:20px;"></td>
                    <td>
                        <select class="input" v-model="selectSourceType">
                            <option :value="source.value" v-for="source in sourceType">{{source.title}}</option>
                        </select>
                    </td>
                    <td><a class="sure" @click="search"></a></td>
                </tr>
            </table>
        </div>
        <a class="btn" href="javascript:;" @click="linkReleaseDynamic"></a>
    </div>

    <div class="cont2">
	    <table width="100%" class="tableList">
            <thead>
                <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
            </thead>  
            <tbody>
                <tr v-for="list in listsInfo">
                    <td>{{list.postTime}}</td>
                    <td>{{list.postSource}}</td>
                    <td class="maxwidth"><div class="maxwidth">{{list.postContext}}</div></td>
                    <td>{{list.seeNum}}</td>
                    <td>{{list.commentNum}}</td>
                    <td>{{list.fansNum}}</td>
                    <td>
                        <div class="operation">
                            <router-link class="btn" :to="{name: 'dynamicDetail', query: {postId: list.postId,roleId: list.roleId}}"></router-link>
                            <a class="btn" href="javascript:;" @click="operationDynamic(list.postId,0)"></a>
                            <a class="btn" href="javascript:;" @click="operationDynamic(list.postId,list.state)">{{list.state==1?'':''}}</a>
                        </div>
                    </td>
                </tr>
            </tbody>     
        </table>

        <div class="fenye">
            <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" layout="total, prev, pager, next" :total="total"></el-pagination>
        </div>

    </div>

  </div>

</template>

<script>
import {util} from '../assets/js/util'

//type012:
//state12:

export default {
  name: 'dynamicList',
  data () {
    return { 
        sourceType: [
            {title: '', value: 1},
            {title: '', value: 3}
        ],
        selectSourceType: 1,
        listsInfo: [],
        allRole: [],
        selectRole: -1,//roleId0
        currentPage: 1,//
        total: 0,//
    }
  },
  created(){
    this.$store.state.updateDynamicList
    this.getAllRole();
    this.getAllDynamic(1);
  },
  watch: {
    /*
    $route(to,from){
        if(from.name=='releaseDynamic'){
            this.getAllDynamic(1);
            console.log('');
        }
    }*/
  },
  methods:{
    //
    getAllRole(){
        let params = {
            method: 'get',
            url: 'user/getRoleInfos',           
            data: {
                adminId: util.getAdminId(),
            }
        }
        util.$http(params).then(response=>{
            console.log(response);
            if(response.data.code=='0000'){
                this.allRole = response.data.data;
            }
        });
    },
    //
    getAllDynamic(page){
        let params = {
            method: 'get',
            url: 'user/getRolePost',           
            data: {
                rowPage: 10,
                page: page,
                adminId: util.getAdminId(),
                roleId: this.selectRole,
                sourceType: this.selectSourceType
            }
        }
        util.$http(params).then(response=>{
            console.log(response);
            if(response.data.code=='0000'){
                this.listsInfo = response.data.data.postInfo;
                this.total = response.data.data.total;
            }
        });
    },
    search(){
        this.currentPage = 1;
        this.getAllDynamic(this.currentPage);
    },
    //
    linkReleaseDynamic(){
        console.log(this.selectRole);
        if(this.selectRole==-1){
            this.$message({message: '',type: 'error'});
            return false;
        }else{  
            this.$router.push({name: 'releaseDynamic', query: {roleId: this.selectRole} });
        }
    },
    //
    operationDynamic(postId,type){
        //type012:
        let text;
        let types;
        if(type==1){
            text = '';
            types = 2;
        }
        if(type==2){
            text = '';
            types = 1;
        }
        if(type==0){
            text = '';
            types = 0;
        }
        let params = {
            url: 'user/updatePost',           
            data: {
                rowPage: 10,
                page: 1,
                adminId: util.getAdminId(),
                roleId: this.selectRole,
                postId: postId,
                type: types,
                source: 1,//12
            }
        }
        console.log(params);

        this.$confirm(`${text}`, '', {
            type: 'warning'
        }).then(() => {
            util.$http(params).then(response=>{
                console.log(response);
                if(response.data.code=='0000'){
                    this.listsInfo = response.data.data.postInfo;
                    this.$message({
                      type: 'success',
                      message: ''
                    });
                }
            });
        });
    },
    handleCurrentChange(page){
        console.log(page);
        this.getAllDynamic(page);
    }
  },
  computed: {
    //
    isUpdateDynamicList(){
        if(this.$store.state.isUpdateDynamicList){
            //console.log(123);
            this.getAllDynamic(1);
            this.$store.state.isUpdateDynamicList = false;
        }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/less/dynamicList.less';
</style>



