<template>
  <div class="productionList">

    <div class="cont1">
        <h3 class="row1">--</h3>
    </div>

    <div class="cont2">
        <h3 class="c-title">--</h3>
    	<table width="100%">
            <tbody>
                <tr>
                    <td class="td-title"><i class="icon-star">*</i><span> </span></td>
                    <td class="td-cont"><input type="text" class="input" placeholder="" v-model="postSource"></td>
                    <td class="td-right">0/50</td>
                </tr>
                <tr>
                    <td class="td-title"><i class="icon-star">*</i><span> </span></td>
                    <td class="td-cont"><input type="text" class="input" placeholder="" v-model="postContext"></td>
                    <td class="td-right">0/200</td>
                </tr>
            </tbody>
        </table>
    </div>

    <div class="cont4">
        <h3 class="c-title">--</h3>
        <div class="row1">
            <dl>
                <dt> </dt>
                <dd>
                    <ul class="radiocheckboxGroup">
                        <li class="radioList" :class="{active: selectFileType==radio.value}" v-for="radio in fileTypeGroup">
                            <input class="inputradio" type="radio" name="fileType" :value="radio.value" v-model="fileType">
                            <span class="icon"></span>
                            <span class="text">{{radio.title}}</span>
                        </li>
                    </ul>
                </dd>
            </dl>
        </div>

    </div>


    <div class="cont4" style="padding-bottom:0;" v-show="isShowPhoto">
        <h3 class="c-title">--</h3>
        <ul class="uploadImgList">
            <li>
                <dl>
                    <dt>
                        <span class="icon-plus"></span>
                        <span class="text"></span>
                    </dt>
                    <dd>
                        <span>JPG/PNG/GIF</span>
                        <span>5MB/</span>
                        <span>9</span>
                    </dd>
                </dl>
                <input class="inputFile" type="file" name="file" multiple="multiple" accept="image/png,image/jpg,image/jpeg" @change="selectFile($event)">
            </li>

            <li v-for="(image,index) in imageUrl">
                <span class="imgbox"><img :src="image"></span>
                <i class="close" @click="deleteImage(index)"></i>
            </li>
<!--             <li>
                <span class="imgbox"><img src="../assets/images/ex1.png"></span>
                <div class="download">
                    <a class="btn"></a>
                    <a class="btn"></a>
                </div>
            </li>
            <li class="list-status2">
                <span class="imgbox"><img src="../assets/images/ex1.png"></span>
                <i class="close"></i>
                <div class="success">
                    <i class="text"></i>
                </div>
            </li>
            <li class="list-status3">
                <span class="imgbox"><img src="../assets/images/ex1.png"></span>
                <i class="close"></i>
                <div class="processing">
                    <i class="text">70%</i>
                </div>
            </li>
            <li class="list-status4">
                <span class="imgbox"><img src="../assets/images/ex1.png"></span>
                <i class="close"></i>
                <div class="processing">
                    <span class="fail">
                        <i class="text"></i>
                        <i class="btn"></i>
                    </span>
                </div>
            </li> -->
        </ul>
    </div>

    <div class="uploadMaxFileProgressbar" v-show="!isShowPhoto">
        <h3 class="c-title">--</h3>
        <div class="row1">
            <dl>
                <dt>
                    <div class="t1">         
                        <a class="btn"><input class="inputFile" type="file" name="file" accept="video/mp4,.avi,.wmv,.mov" @change="selectFile1($event)"></a>
                        <a class="s1">MP4AVIWMVMOV50MB</a>
                    </div>
                </dt>
            </dl>
        </div>
        <div class="row2" v-show="videoName">
            <h6 class="t1">{{videoName}}</h6>
            <div class="t2">
                <span class="s1">{{currentPercent}}%</span>          
                <span class="s2">{{currentSize}}MB/{{videoSize}}MB</span>
                <span class="s3" @click="deleteVideo"></span>
            </div>
            <div class="t3">
                <span class="bgbar"></span>
                <span class="processbar" :style="{width: currentPercent+'%'}"></span>
            </div>

            <div id="videoPreview"></div>
        </div>
    </div>

    <div class="cont-last">
        <a class="btn" @click="submit"></a>
        <span class="errorTips" v-if="isShowErrorMessage">{{errorMessage}}</span>
    </div>


  </div>

</template>

<script>
import {util} from '../assets/js/util'
import OSS from "ali-oss"
import base64 from "base-64"

export default {
    name: 'releaseDynamic',
    data() {
        return {
            isShowErrorMessage: false,
            errorMessage: '',
            fileTypeGroup: [
                {title: '', value: 1},
                {title: '', value: 3},
            ],
            roleId: '',
            fileType: 1,//
            isShowPhoto: true,
            aliData: {},//
            postSource: '',//
            postContext: '',//
            maxImageSize: 5*1024*1024,//5M
            maxVideoSize: 50*1024*1024,//50M
            videoName: '',//
            videoSize: '',//
            videoTime: 0,//
            progressing: 0,//0-1
            currentPercent: 0,//
            currentSize: 0,//
            imageUrl: [],//
            videoUrl: '',//
        }
    },
    created(){
        //
        console.log(this.$route.query.roleId);
        this.roleId = this.$route.query.roleId;
        let params = {
            url: '/post/getAliKey',
        }
        util.$http(params).then(response=>{
            console.log(response);
            if(response.data.code=='0000'){
                this.aliData = response.data.data;
            }
        }); 
    },
    methods:{
        //
        deleteImage(index){
            //console.log(this.imageUrl);
            let imageUrl = this.imageUrl;
            imageUrl.splice(index,1);
            this.imageUrl = imageUrl;
            //console.log(this.imageUrl);
        },
        selectFile(event){
            let that = this;

            let files = event.target.files;
            let filesLength = files.length;
            console.log(files);
            console.log(filesLength);

            let rightFiles = [];//
            for(let i=0;i<filesLength;i++){
                if(files[i].size > this.maxImageSize){
                  this.$message({showClose: true, message: ',5M!',type: 'error', duration: 2000});
                }else{
                    rightFiles.push(files[i]);
                }
            }
            console.log(rightFiles);

            let endpoint = base64.decode(this.aliData.endpoint);
            let accessKeyId = base64.decode(this.aliData.accessKeyId);
            let accessKeySecret = base64.decode(this.aliData.accessKeySecret);
            let bucket = base64.decode(this.aliData.bucket);

            let OSS = require('ali-oss');
            let client = new OSS({
                region : endpoint,
                endpoint : endpoint,
                accessKeyId : accessKeyId,
                accessKeySecret : accessKeySecret,
                bucket : bucket
            });

            async function multipartUpload (file) {
              try {
                for(let i=0;i<file.length;i++){
                  let storeAs = new Date().getTime() + '.png';
                  let result = await client.multipartUpload(storeAs, file[i], {
                    partSize: 1024*1024
                  });
                  console.log(result);
                  that.imageUrl.push(result.res.requestUrls[0].replace(/\?.{0,}$/,""));
                }
              } catch (e) {
                if (e.code === 'ConnectionTimeoutError') {
                  console.log("!");
                }
                console.log(e)
              }
            }
            multipartUpload(rightFiles);
        },
        //
        deleteVideo(){
            this.videoName = '';
            this.videoUrl = '';
        },
        //
        selectFile1(event){
            const that = this;
            let file = event.target.files[0];
            console.log(file);
            if (file.size > this.maxVideoSize) {
              this.$message({showClose: true, message: ',50M!',type: 'error', duration: 2000});
              return false;
            }

            var videoPreview = document.getElementById("videoPreview");
            var fileurl = window.URL.createObjectURL(file);
            var createVideo = document.createElement("video");
            createVideo.setAttribute("src", fileurl);
            createVideo.setAttribute("controls", "controls");
            createVideo.setAttribute("width", "100%");
            videoPreview.appendChild(createVideo);
            createVideo.ondurationchange = function() {
                that.videoTime = createVideo.duration;
            }

            this.videoName = file.name;
            this.videoSize = (file.size/1024/1024).toFixed(2);

            let endpoint = base64.decode(this.aliData.endpoint);
            let accessKeyId = base64.decode(this.aliData.accessKeyId);
            let accessKeySecret = base64.decode(this.aliData.accessKeySecret);
            let bucket = base64.decode(this.aliData.bucket);

            let OSS = require('ali-oss');
            let client = new OSS({
                region : endpoint,
                endpoint : endpoint,
                accessKeyId : accessKeyId,
                accessKeySecret : accessKeySecret,
                bucket : bucket
            });

            let type = file.name.split('.')[1];
            let storeAs = new Date().getTime() + '.' + type;

            async function multipartUpload (storeAs,file) {
              try {
                let result = await client.multipartUpload(storeAs, file, {
                    progress,
                    partSize: 1024*1024
                  });
                  console.log(result);
                  that.videoUrl = result.res.requestUrls[0].replace(/\?.{0,}$/,"");
              } catch (e) {
                if (e.code === 'ConnectionTimeoutError') {
                  console.log("!");
                }
                console.log(e)
              }
            }
            let progress = progressing=>{
                console.log(progressing);
                this.progressing = progressing;
                this.currentPercent = (progressing*100).toFixed(0);
                this.currentSize = (this.videoSize*progressing).toFixed(2);
            }
            multipartUpload(storeAs,file);
        },
        submit(){
            const that = this;
            this.isShowErrorMessage = false;

            if(util.trim(this.postSource).length<1){
                this.isShowErrorMessage = true;
                this.errorMessage = '';
                return false;
            }
            if(util.trim(this.postContext).length<1){
                this.isShowErrorMessage = true;
                this.errorMessage = '';
                return false;
            }

            //console.log(this.imageUrl);
            let postUrl = '';
            if(this.fileType==1){
                if(this.imageUrl.length<1){
                    this.isShowErrorMessage = true;
                    this.errorMessage = '';
                    return false;
                }
                postUrl = this.imageUrl.join(",");
            }else if(this.fileType==3){
                if(util.trim(this.videoUrl).length<5){
                    this.isShowErrorMessage = true;
                    this.errorMessage = '';
                    return false;
                }
                postUrl = this.videoUrl;
            }

            let params = {
                url: 'post/addPost',
                data: {
                    roleId: this.roleId,
                    postType: this.fileType,
                    postSource: this.postSource,
                    postContext: this.postContext,
                    postUrl: postUrl,
                    videoTime: this.videoTime//
                }
            }
            //console.log(params);
            util.$http(params).then(response=>{
                console.log(response);
                if(response.data.code=='0000'){
                    this.$message({
                        type: 'success',
                        message: '',
                        duration: 1000,
                        onClose: function(){
                            //that.$router.push({name: 'dynamicList'});
                            console.log(that.$store.state.isUpdateDynamicList);
                            that.$store.state.isUpdateDynamicList = true;
                            that.$router.go(-1);
                        }
                    });
                }
            }); 
        }
    },
    computed:{
        selectFileType(){
            console.log(this.fileType);
            if(this.fileType==1){
                this.isShowPhoto = true;
            }else{
                this.isShowPhoto = false;
            }
            return this.fileType;
        }
    }
}
</script>

<style lang="less" scoped>
@import '../assets/less/releaseDynamic.less';
</style>



